.welcome {
  text-align: center;
  font-size: large;
}

.welcome .header {
  background: -webkit-gradient(linear, left top, left bottom, from(blue), to(fuchsia));
  background: -webkit-linear-gradient(top, blue, fuchsia);
  background: -o-linear-gradient(top, blue, fuchsia);
  background: linear-gradient(to bottom, blue, fuchsia);
  padding: 20px;
  color: white;
}

.welcome label span {
  display: block;
  margin: 10px;
  line-height: 1.5em;
}

.welcome input {
  display: block;
  width: 100%;
  text-align: center;
  margin: 0 auto;
}

.welcome button {
  margin-top: 50px;
  font-size: large;
  -webkit-border-image: -webkit-gradient(linear, left top, left bottom, from(fuchsia), to(blue));
  -webkit-border-image: -webkit-linear-gradient(top, fuchsia, blue);
       -o-border-image: -o-linear-gradient(top, fuchsia, blue);
          border-image: -webkit-gradient(linear, left top, left bottom, from(fuchsia), to(blue));
          border-image: linear-gradient(to bottom, fuchsia, blue);
  border-image-slice: 1;
  color: fuchsia;
}



