.game input {
  display: block;
  width: 100%;
}

.game .album-progress {
  font-size: 1.5em;
  text-decoration: underline;
  text-align: center;
}

.game .answer-stats ul {
  padding: 0;
}

.game .answer-stats .stat {
  margin-top: 10px;
}

.game .answer-stats .answered { color: aqua; }
.game .answer-stats .correct { color: lime; }
.game .answer-stats .incorrect { color: fuchsia; }
.game .answer-stats .givenup { color: gray; }

.game button {
  margin-top: 10px;
  margin-right: 10px;
  background: transparent;
}

.game input, .game button.answer {
  color: yellow;
  border-color: yellow;
}

.game input.incorrect {
  color: fuchsia;
  border-color: fuchsia;
}

.game input.correct {
  color: lime;
  border-color: lime;
}

.game input:disabled {
  color: gray;
  border-color: gray;
}

.game .feedback {
  margin-top: 10px;
}

.game .feedback.correct {
  color: lime;
}

.game .feedback.incorrect {
  color: fuchsia;
}

.game .feedback.givenup {
  color: gray;
}

.game button.giveup {
  color: fuchsia;
  border-color: fuchsia;
}

.game button.quit {
  color: red;
  border-color: red;
}

.game canvas {
  margin-top: 10px;
}



